#home {
  display: flex;
  flex-direction: column;
  align-items: center;

  .profile {
    width: 210px;
    height: 210px;

    img {
      width: 100%;
      height: 100%;
      border-radius: 50%;
      object-fit: cover;
    }
  }
  .profile_text {
    margin-top: 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;

    h3 {
      color: #fff;
      font-size: 2rem;
      @media screen and (max-width: 500px) {
        margin-top: 35px;
        font-size: 1.5rem;
        text-align: center;
      }
      span {
        color: #1dddbd;
      }
    }
    .job {
      color: #fff;
      opacity: 0.5;
      font-size: 0.85rem;
    }
    .text {
      margin-top: 2rem;
      color: #fff;
      text-align: center;
      font-size: 5rem;
      line-height: 1.3;

      @media screen and (max-width: 800px) {
        font-size: 3.5rem;
        margin-top: 2rem;
      }
      @media screen and(max-width: 530px) {
        font-size: 2.5rem;
      }
    }
    a {
      margin-top: 3rem;
      text-decoration: none;
      color: #000;
      background: #fff;
      padding: 0.65rem 1rem;
      border-radius: 10px;
      font-size: 0.85rem;
      text-transform: capitalize;
    }
    div {
      color: #fff;
      padding: 0.35rem 1rem;
      border-radius: 25px;
      font-size: 0.85rem;
      cursor: pointer;
    }
    @media screen and (max-width: 800px) {
      div {
        display: none;
      }
    }
    .titles {
      padding: 10px;
      margin: 20px;
      display: flex;
      flex-direction: row;
    }
    .web {
      background: rgba(255, 255, 255, 0.15);
      right: -1rem;
      margin: 20px;
    }
    .mobile {
      background: rgba(255, 255, 255, 0.15);

      margin: 20px;
      left: 2rem;
    }
    .python {
      background: rgba(255, 255, 255, 0.15);
      margin: 20px;
      left: 0;
    }
  }
}
